import React, { useState } from 'react';

const Publications = () => {
  const [activeYear, setActiveYear] = useState('2025');

  const years = ['2025', '2024', '2023', '2022'];

  const publications = [
    {
      id: 1,
      year: '2025',
      title: '      ',
      authors: ' . .,  . .,  . .',
      journal: '   ',
      issue: ' 1 (2025)',
      type: 'article',
      abstract: '           ,       .',
      pdf: '/publications/modern-biosafety-2025.pdf',
      featured: true
    },
    {
      id: 2,
      year: '2025',
      title: '       ',
      authors: ' . .,  . .',
      journal: '  ',
      issue: ' 2 (2025)',
      type: 'research',
      abstract: '            .',
      pdf: '/publications/epidemiology-2025.pdf',
      featured: false
    },
    {
      id: 3,
      year: '2024',
      title: '       ',
      authors: ' . .,  . .',
      journal: '    ',
      issue: ' 2024 .',
      type: 'guideline',
      abstract: '           .',
      pdf: '/publications/guidelines-2024.pdf',
      featured: false
    },
    {
      id: 4,
      year: '2024',
      title: '   :    ',
      authors: ' . .,  . .',
      journal: '-   ',
      issue: ' 3,  4 (2024)',
      type: 'article',
      abstract: '            .',
      pdf: '/publications/amr-2024.pdf',
      featured: false
    }
  ];

  const filteredPublications = publications.filter(pub => pub.year === activeYear);

  const getTypeLabel = (type) => {
    switch (type) {
      case 'article': return '';
      case 'research': return '';
      case 'guideline': return ' ';
      default: return '';
    }
  };

  const getTypeColor = (type) => {
    switch (type) {
      case 'article': return 'bg-blue-100 text-blue-800';
      case 'research': return 'bg-green-100 text-green-800';
      case 'guideline': return 'bg-purple-100 text-purple-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  return (
    <section id="publications" className="py-16 bg-white">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="text-center mb-12">
          <h2 className="text-3xl md:text-4xl font-bold text-gray-900 mb-4">
             
          </h2>
          <p className="text-lg text-gray-600 max-w-3xl mx-auto">
                     
          </p>
        </div>

        {/*    */}
        <div className="flex justify-center mb-12">
          <div className="bg-gray-100 rounded-lg p-1 border border-gray-200">
            {years.map((year) => (
              <button
                key={year}
                onClick={() => setActiveYear(year)}
                className={`px-6 py-3 rounded-md font-medium text-base transition-all duration-300 ${
                  activeYear === year
                    ? 'bg-white text-gray-900 shadow-sm border border-gray-200'
                    : 'text-gray-600 hover:text-gray-900 hover:bg-white/50'
                }`}
              >
                {year} 
              </button>
            ))}
          </div>
        </div>

        {/*  */}
        <div className="grid lg:grid-cols-2 gap-8 max-w-6xl mx-auto">
          {filteredPublications.map((publication) => (
            <article
              key={publication.id}
              className={`bg-white rounded-xl shadow-sm border border-gray-200 overflow-hidden hover:shadow-lg transition-all duration-300 ${
                publication.featured ? 'ring-2 ring-blue-500' : ''
              }`}
            >
              <div className="p-6">
                <div className="flex items-start justify-between mb-4">
                  <span className={`inline-flex items-center px-3 py-1 rounded-full text-xs font-medium ${getTypeColor(publication.type)}`}>
                    {getTypeLabel(publication.type)}
                  </span>
                  {publication.featured && (
                    <span className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium bg-yellow-100 text-yellow-800">
                       
                    </span>
                  )}
                </div>

                <h3 className="font-bold text-xl text-gray-900 mb-3 leading-tight">
                  {publication.title}
                </h3>

                <div className="mb-4">
                  <p className="text-sm text-gray-600 mb-1">
                    <strong>:</strong> {publication.authors}
                  </p>
                  <p className="text-sm text-gray-600 mb-1">
                    <strong>:</strong> {publication.journal}
                  </p>
                  <p className="text-sm text-gray-600">
                    <strong>:</strong> {publication.issue}
                  </p>
                </div>

                <p className="text-gray-600 mb-6 leading-relaxed">
                  {publication.abstract}
                </p>

                <div className="flex items-center justify-between">
                  <span className="text-sm text-gray-500">
                      {publication.year} 
                  </span>
                  <button className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg font-medium text-sm transition-colors flex items-center">
                    <svg className="w-4 h-4 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 10v6m0 0l4-4m-4 4l-4-4m8 2a9 9 0 11-18 0 9 9 0 0118 0z" />
                    </svg>
                     PDF
                  </button>
                </div>
              </div>
            </article>
          ))}
        </div>

        {/*   */}
        <div className="mt-16 bg-gradient-to-r from-gray-50 to-gray-100 rounded-xl p-8 text-center">
          <h3 className="text-2xl font-bold text-gray-900 mb-4">
             
          </h3>
          <p className="text-gray-600 mb-6 max-w-2xl mx-auto">
                         PDF
          </p>
          <div className="flex flex-col sm:flex-row gap-4 justify-center">
            <button className="bg-blue-600 hover:bg-blue-700 text-white font-semibold py-3 px-8 rounded-lg transition-colors">
                 (PDF)
            </button>
            <button className="border-2 border-blue-600 text-blue-600 hover:bg-blue-50 font-semibold py-3 px-8 rounded-lg transition-colors">
                 
            </button>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Publications;
