import React, { useState } from 'react';

const News = () => {
  const [activeCategory, setActiveCategory] = useState('all');

  const categories = [
    { id: 'all', name: ' ', count: 12 },
    { id: 'seminars', name: '  ', count: 5 },
    { id: 'research', name: ' ', count: 4 },
    { id: 'guidelines', name: ' ', count: 3 }
  ];

  const news = [
    {
      id: 1,
      category: 'seminars',
      title: '   ',
      date: '15  2025',
      excerpt: '24  2025      :         .',
      image: '/api/placeholder/400/250',
      featured: true
    },
    {
      id: 2,
      category: 'research',
      title: '    ',
      date: '10  2025',
      excerpt: '        2025 .',
      image: '/api/placeholder/400/250',
      featured: false
    },
    {
      id: 3,
      category: 'guidelines',
      title: '   ',
      date: '5  2025',
      excerpt: '          .',
      image: '/api/placeholder/400/250',
      featured: false
    },
    {
      id: 4,
      category: 'seminars',
      title: '    ',
      date: '1  2025',
      excerpt: ' 15  17  2025   .       .',
      image: '/api/placeholder/400/250',
      featured: false
    }
  ];

  const filteredNews = activeCategory === 'all'
    ? news
    : news.filter(item => item.category === activeCategory);

  return (
    <section id="news" className="py-16 bg-gray-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="text-center mb-12">
          <h2 className="text-3xl md:text-4xl font-bold text-gray-900 mb-4">
              
          </h2>
          <p className="text-lg text-gray-600 max-w-3xl mx-auto">
                ,        
          </p>
        </div>

        {/*   */}
        <div className="flex flex-wrap justify-center gap-4 mb-12">
          {categories.map((category) => (
            <button
              key={category.id}
              onClick={() => setActiveCategory(category.id)}
              className={`px-6 py-3 rounded-lg font-medium text-sm transition-all duration-300 ${
                activeCategory === category.id
                  ? 'bg-blue-600 text-white shadow-lg'
                  : 'bg-white text-gray-700 hover:bg-blue-50 hover:text-blue-600 border border-gray-200'
              }`}
            >
              {category.name} ({category.count})
            </button>
          ))}
        </div>

        {/*  */}
        <div className="grid lg:grid-cols-2 gap-8 max-w-6xl mx-auto">
          {filteredNews.map((item) => (
            <article
              key={item.id}
              className={`bg-white rounded-xl shadow-sm border border-gray-200 overflow-hidden hover:shadow-lg transition-all duration-300 ${
                item.featured ? 'ring-2 ring-blue-500' : ''
              }`}
            >
              <div className="aspect-video bg-gradient-to-br from-blue-100 to-cyan-100 flex items-center justify-center">
                <svg className="w-12 h-12 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
                </svg>
              </div>

              <div className="p-6">
                <div className="flex items-center justify-between mb-3">
                  <span className="inline-flex items-center px-3 py-1 rounded-full text-xs font-medium bg-blue-100 text-blue-800">
                    {categories.find(cat => cat.id === item.category)?.name}
                  </span>
                  {item.featured && (
                    <span className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium bg-yellow-100 text-yellow-800">
                      
                    </span>
                  )}
                </div>

                <h3 className="font-bold text-xl text-gray-900 mb-3 leading-tight">
                  {item.title}
                </h3>

                <p className="text-gray-600 mb-4 leading-relaxed">
                  {item.excerpt}
                </p>

                <div className="flex items-center justify-between">
                  <span className="text-sm text-gray-500">
                    {item.date}
                  </span>
                  <button className="text-blue-600 hover:text-blue-800 font-medium text-sm transition-colors">
                      
                  </button>
                </div>
              </div>
            </article>
          ))}
        </div>

        {/*     */}
        <div className="text-center mt-12">
          <button className="bg-white border-2 border-blue-600 text-blue-600 hover:bg-blue-50 font-semibold py-3 px-8 rounded-lg transition-all duration-300">
              
          </button>
        </div>
      </div>
    </section>
  );
};

export default News;
